.gallery.container {
	max-width: 90%;
}

$galleria: true;
$wookmark: true;
$colorbox: true;
$indented-lists: false;
$page-bg: white;
$sans: true;

.loading {
	background-image: url(https://img.gtww.net/static/loading_ew34.gif);
 	width: 50%;
	margin: 0 auto;
}

@if $galleria {
	// Remove shadow box for galleria pictures
	.galleria-container img {
		border: none;
		border-radius: 0;
		box-shadow: none;
	}
	.galleria {
		margin: 15px 0;
	}
}

@if $wookmark {
	#wookmark {
		height: 1200px;
		position: relative;
		padding: 30px 0 30px 0;
		#tiles {
			list-style-type: none;
			position: relative;
			margin: 0;
			li {
				width: 200px;
				//display: none;
				cursor: pointer;
				padding: 4px;
				img {
					display: block;
				}
				p {
					color: #666;
					font-size: 12px;
					margin: 7px 0 0 7px;
				}				
			}
			ali:nth-child(3n) {
				height: 175px;
			}
			ali:nth-child(4n-3) {
				padding-bottom: 30px;
			}
			ali:nth-child(5n) {
				height: 250px;
			}
		}
	}
}

@if $colorbox{
/* 
 Used to prevent weird stuff from getting inherited
*/
#colorbox, #cboxWrapper {border:0;background:none;}
#cboxPrevious, #cboxNext, #cboxClose, #cboxSlideshow, #cboxTitle, #cboxCurrent {font-size:14px;}
/*
 ColorBox Core Style:
 The following CSS is consistent between example themes and should not be altered.
*/
#colorbox, #cboxOverlay, #cboxWrapper{position:absolute; top:0; left:0; z-index:9999; overflow:hidden;}
#cboxOverlay{position:fixed; width:100%; height:100%;}
#cboxMiddleLeft, #cboxBottomLeft{clear:left;}
#cboxContent{position:relative;}
#cboxLoadedContent{overflow:auto; -webkit-overflow-scrolling: touch;}
#cboxTitle{margin:0;}
#cboxLoadingOverlay, #cboxLoadingGraphic{position:absolute; top:0; left:0; width:100%; height:100%;}
#cboxPrevious, #cboxNext, #cboxClose, #cboxSlideshow{cursor:pointer;}
.cboxPhoto{float:left; margin:auto; border:0; display:block; max-width:none;}
.cboxIframe{width:100%; height:100%; display:block; border:0;}
#colorbox, #cboxContent, #cboxLoadedContent{box-sizing:content-box; -moz-box-sizing:content-box; -webkit-box-sizing:content-box;}

/* 
 User Style:
 Change the following styles to modify the appearance of ColorBox. They are
 ordered & tabbed in a way that represents the nesting of the generated HTML.
*/
#cboxOverlay{background:#fff;}
#colorbox{outline:0;}
 #cboxContent{margin-top:32px; overflow:visible; background:#000;}
 .cboxIframe{background:#fff;}
 #cboxError{padding:50px; border:1px solid #ccc;}
 #cboxLoadedContent{background:#000; padding:1px;}
 #cboxLoadingGraphic{background:url(https://img.gtww.net/static/loading_ew34.gif) no-repeat center center;}
 #cboxLoadingOverlay{background:#000;}
 #cboxTitle{position:absolute; top:-22px; left:0; color:#000;}
 #cboxCurrent{position:absolute; top:-22px; right:205px; text-indent:-9999px;}

 /* these elements are buttons, and may need to have additional styles reset to avoid unwanted base styles */
 #cboxPrevious, #cboxNext, #cboxSlideshow, #cboxClose {border:0; padding:0; margin:0; overflow:visible; text-indent:-9999px; width:20px; height:20px; position:absolute; top:-20px; background:url(https://img.gtww.net/static/colorbox_controls_qw87.png) no-repeat 0 0;}
 
 /* avoid outlines on :active (mouseclick), but preserve outlines on :focus (tabbed navigating) */
 #cboxPrevious:active, #cboxNext:active, #cboxSlideshow:active, #cboxClose:active {outline:0;}

 #cboxPrevious{background-position:0px 0px; right:44px;}
 #cboxPrevious:hover{background-position:0px -25px;}
 #cboxNext{background-position:-25px 0px; right:22px;}
 #cboxNext:hover{background-position:-25px -25px;}
 #cboxClose{background-position:-50px 0px; right:0;}
 #cboxClose:hover{background-position:-50px -25px;}
 .cboxSlideshow_on #cboxPrevious, .cboxSlideshow_off #cboxPrevious{right:66px;}
 .cboxSlideshow_on #cboxSlideshow{background-position:-75px -25px; right:44px;}
 .cboxSlideshow_on #cboxSlideshow:hover{background-position:-100px -25px;}
 .cboxSlideshow_off #cboxSlideshow{background-position:-100px 0px; right:44px;}
 .cboxSlideshow_off #cboxSlideshow:hover{background-position:-75px -25px;}
}

sub, sup {
	line-height: 1.1;
}

@if $indented-lists {
	ul, ul ul, ul ol, ol, ol ul, ol ol {
		margin-left: 2em;
	}
}

#content .blog-index article h1, h1.entry-title {
	font-size: 1.5em;
}

#content > div,
#content > article {
	padding-top: 0px;
}

html {
	background: $page-bg;
}

/* Adjust margins */
article header {
	margin-top: 0;
	margin-bottom: 1.1em;
}

h1, h2, h3, h4, h5, h6 {
	margin-bottom: 0.5em;
	margin-top: 1.3em;
}

p {
	margin-bottom: 0;
	margin-top: 1em;
}

ul, ol {
	margin-top: 0.5em;
}

ol ul, ol ol, ul ul, ul ol {
 margin-top: 0;
 margin-bottom: 0;
}
figure.code {
	margin: 0.7em 0;
}

/* Reduce footer size */
footer[role="contentinfo"] {
	margin: 0 0 10px;
}

/* Round the top corners */
body > nav[role="navigation"] {
	border-top-left-radius: 0.4em;
	border-top-right-radius: 0.4em;
}

/* Use same font for headings */
.heading, body > header h1, h1, h2, h3, h4, h5, h6 {
	font-family: $sans	
}
